import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import util from "../../../../utils/util.js";
import api from "../../../../utils/api.js";
export default {
	components:{
		uniListItem
	},
	data() {
		return {
			showCon:false,
			mask:false,
			array:'',
			bott:'',
			txts:{
				j:'提现记录',
				m:'提现金额',
				t:'提现方式',
				n:'普通转账',
				s:'预计确认提现后，24小时内到账',
				z:'绑定微信信息',
				g:'绑定支付宝信息'
			},
			navs:['转出到微信','转出到支付宝','转出到银行卡'],
			navIndex: 0,  
			moreMoney:5000,       // 最多提取金额
			amount:'',          // 提取金额
			isTransfer: true,  // 是否转账
			infoRes:'',        // 微信授权信息
			userInfo:'',       // 用户微信资料
			encryptedData:'',
			iv:'',
			session_key:'',
			open_id:'',
			wxIsBind: false,
			code_id:'',
			code_msg:'',
			now_amount:'',
			pageIndex:0,
			wxNickName:'',
			bankCardInfo:''
		};
	},
	onLoad(options) {
		this.$data.now_amount = options.now_amount;
		this.$data.pageIndex = options.pageIndex;
		if(this.$data.pageIndex == 1){  // 佣金提现
			uni.setNavigationBarTitle({
				title: "佣金提现"
			})
		}else if(this.$data.pageIndex == 2){   // 代采提现
			uni.setNavigationBarTitle({
				title: "代采提现"
			})
		}else if(this.$data.pageIndex == 3){
			uni.setNavigationBarTitle({
				title: "小鹿家人提现"
			})
		}
	},
	onShow() {
		
		let navIndex = this.$data.navIndex;
		if(navIndex == 0){
			this.apiMemberWxBind(navIndex);
		}else if(navIndex == 1){
			this.memberAliBind(navIndex);
		}else if(navIndex == 2){
			this.memberBankCardBind(navIndex);
		}
		
	},
	methods: {
		// 修改银行卡绑定信息
		editBankCardInfo(){
			uni.navigateTo({
				url:'../bindBankCard/bindBankCard?edit=1'
			})
		},
		// 获取微信绑定信息
		apiMemberWxBind(index){
			api.apiMemberWxBind({}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					this.$data.navIndex = index;
					this.$data.wxIsBind = true;
					this.$data.userInfo = res.data;
					this.$data.wxNickName = res.data.nickName
				}else{
					this.$data.wxIsBind = false;
				}
			}).catch((res)=>{
				this.$data.wxIsBind = false;
			})
		},
		// 关闭授权
		changeModalCancel() {
			this.$data.showCon = false;
			let _this = this;
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				console.log(loginRes.authResult);
				// 获取用户信息
				//发起网络请求
				let data = {
				  code: loginRes.code,
				  from: 3
				}
				
				uni.getUserInfo({
				  provider: 'weixin',
				  success: function (infoRes) {
					_this.$data.infoRes = infoRes;
					_this.$data.userInfo = infoRes.userInfo;
					_this.$data.encryptedData = infoRes.encryptedData;
					_this.$data.iv   = infoRes.iv;
					api.getOpenId({
					  data
					}).then((res) => {
					  if (res.code == 200 || res.code == 0) {
						uni.setStorageSync('open_id', res.data.openid);
						_this.$data.session_key = res.data.session_key;
						_this.$data.open_id = res.data.openid;
						_this.$data.wxIsBind = true;
						
						
						if(!_this.$data.wxIsBind){
							util.errorTips('请先绑定用户微信信息');
							return false;
						}
						
						if(_this.$data.encryptedData == "" || _this.$data.iv == "" || _this.$data.session_key == "" || _this.$data.open_id == ""){
							_this.$data.showCon = true;
							return false;
						}
						api.apiMemberWxBind({
							method:'POST',
							data:{
								openid: _this.$data.open_id,
								session_key: _this.$data.session_key,
								iv: _this.$data.iv,
								encryptedData: _this.$data.encryptedData,
							}
						}).then((res)=>{
							if(res.code == 200 || res.code == 0){
								util.successTips(res.msg);
							}else{
								util.errorTips(res.msg);
							}
						}).catch((res)=>{
							util.errorTips(res.msg);
						})
						
					  } else {
						util.errorTips(res.msg)
					  }
					})
					
				  }
				});
			  }
			});
		},
		password(num){
			let _this = this;
			if(this.$data.array.length < 6){
				this.$data.array= this.$data.array + num;
				console.log(this.$data.array)
			}
			if(this.$data.array.length == 6){
				console.log('跳转')
				this.$data.mask = false
				
				this.$data.bott = ''
				
				let data = {
					"id": this.$data.code_id,
					"code": this.$data.array,
					"mobile": uni.getStorageSync('user_name'),
					"type": "wx",
					"amount": this.$data.amount,
				}
				if(this.$data.navIndex == 0){
					data.type = "wx"
				}
				if(this.$data.navIndex == 1){
					data.type = "ali"
				}
				if(this.$data.navIndex == 2){
					data.type = "bank"
				}
				if(this.$data.pageIndex == 1){
					data.asset_type = 'commission';
				}else if(this.$data.pageIndex == 2){
					data.asset_type = 'replace';
				}else if(this.$data.pageIndex == 3){
					data.asset_type = 'marketing';
				}
				api.apiAssetTake({
					method:'POST',
					data
				}).then((res)=>{
					if(res.code == 200 || res.code == 0){
						_this.$data.array = ''
						uni.navigateBack({
							delta:1
						})
					}else{
						_this.$data.array = '';
						util.errorTips(res.msg);
					}
				}).catch((res)=>{
					_this.$data.array = ''
					util.errorTips(res.msg || res.message);
				})	
				
				}
		},
		reset(){
			this.$data.array = []
		},
		backspace(){
			let data = this.$data.array;
			data = data.substr(0,data.length-1);
			this.$data.array = data;
		},
		masks(){
			 let _this = this;
			 
			 if(this.$data.navIndex == 0){
				 if(!this.$data.wxIsBind){
				 	if(_this.$data.encryptedData == "" || _this.$data.iv == "" || _this.$data.session_key == "" || _this.$data.open_id == ""){
				 		_this.$data.showCon = true;
				 		util.errorTips('请绑定用户微信信息');
				 		return false;
				 	}
				 }
			 }
			
			
			if(_this.$data.amount == ""){
				util.errorTips('请填写提现金额');
				return false;
			}
			
			if(_this.$data.amount > parseFloat(_this.$data.now_amount)){
				util.errorTips('转出金额不能大于提现金额');
				return false;
			}
			
			
			
			api.regSMS({
				method:'POST',
				data:{
					mobile:uni.getStorageSync('user_name')
				}
			}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					_this.$data.code_id = res.data.id;
					
					_this.mask = true;
					setTimeout( function(){
						_this.bott = 'bot'
					},50)
					
				}
			}).catch((res)=>{
				util.errorTips(res.msg);
			})
			
		},
		maskss(){
			this.mask = false
			this.bott = ''
			this.array = [];
			
		},
		goCashRecord(){
			uni.navigateTo({
				url:'../cashRecord/cashRecord?pageIndex='+ this.$data.pageIndex
			})
		},
		bindWxInfo(){
			// uni.navigateTo({
			// 	url:'../bindAlipay/bindAlipay'
			// })
			this.$data.showCon = true;
			
		},
		getOutMoney(){
			
		},
		getMoreMoney(){
			if(this.$data.now_amount >= 5000){
				this.$data.amount = 5000;
			}else{
				this.$data.amount = this.$data.now_amount;
			}
			
		},
		checkTransfer(){
			this.$data.isTransfer = !this.$data.isTransfer;
		},
		
		// 获取支付宝绑定信息
		memberAliBind(index){
			api.memberAliBind({}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					this.$data.userInfo.nickName = res.data.real_name;
					this.$data.navIndex = index;
				}else{
					uni.showModal({
						title: '提示',
						content: '绑定支付宝?',
						confirmText:'立即绑定',
						cancelColor:'#000000',
						success: function (res) {
							if (res.confirm) {
								uni.navigateTo({
									url:'../bindAlipay/bindAlipay'
								})
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				}
			}).catch((res)=>{
				uni.showModal({
					title: '提示',
					content: '绑定支付宝?',
					confirmText:'立即绑定',
					cancelColor:'#000000',
					success: function (res) {
						if (res.confirm) {
							uni.navigateTo({
								url:'../bindAlipay/bindAlipay'
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			})
		},
		// 获取支付宝绑定信息
		memberBankCardBind(index){
			
			api.memberBankCardBind({}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					this.$data.bankCardInfo = res.data;
					this.$data.navIndex = index;
				}else{
					uni.showModal({
						title: '提示',
						content: '绑定银行卡?',
						confirmText:'立即绑定',
						cancelColor:'#000000',
						success: function (res) {
							if (res.confirm) {
								uni.navigateTo({
									url:'../bindBankCard/bindBankCard'
								})
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				}
			}).catch((res)=>{
				
				uni.showModal({
					title: '提示',
					content: '绑定银行卡?',
					confirmText:'立即绑定',
					cancelColor:'#000000',
					success: function (res) {
						if (res.confirm) {
							uni.navigateTo({
								url:'../bindBankCard/bindBankCard'
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			})
			
			
			
		},
		checkNav(index) {
			if(index == 1){
				this.memberAliBind(index);
			}else if(index == 2){
				this.memberBankCardBind(index);
			}else if(index == 0){
				this.apiMemberWxBind(index);
			}
			
		}
	},
}
